import Layout from '@/views/Layout'

const _import_ = file => () => import('@/views/' + file + '.vue')
/*
 * 公共路由表, 一般情况这里无需任何修改.
 * 除非你需要配置更多任何角色或权限下都可以访问的路由
 */
export const constantRouterMap = [{
    path: '/login',
    component: _import_('Login/index'),
    hidden: true
  },
  {
    path: '',
    component: Layout,
    redirect: '/dash',
    children: [{
      path: 'dash',
      name: 'Dashboard',
      component: _import_('Dashboard/index'),
      meta: {
        title: '主页',
        icon: 'fdddfont icon-home',
        noCache: false
      }
    }]
  },
  {
    path: '',
    component: Layout,
    hidden: true,
    children: [{
      path: 'personal-settings',
      name: 'PersonalSettings',
      component: _import_('Layout/Components/Settings/index'),
      meta: {
        title: '个性化配置',
        icon: 'fdddfont icon-custom-settings',
        noCache: true
      }
    }]
  },
  {
    path: '/modify-password',
    component: _import_('Layout/Components/ModifyPassword/index'),
    hidden: true,
    meta: {
      title: '修改密码',
      icon: 'fdddfont icon-home',
      noCache: true
    }
  },
  {
    path:'',
    component: Layout,
    children: [{
      path: 'jujiao',
      name: 'JuJiao',
      component: _import_('Development/JuJiao/index'),
      meta: {
        title: '聚焦',
        icon: 'fdddfont icon-home',
        noCache: false
      }
    }]
  },
  {
    path: '/401',
    component: _import_('others/401'),
    hidden: true
  },
  {
    path: '*',
    component: _import_('others/404'),
    hidden: true
  },
  {
    path: '/success',
    component: _import_('others/signSuccess'),
    hidden: true
  },
  {
    path: '/fail',
    component: _import_('others/signFail'),
    hidden: true
  },
  {
    path: '/system-management',
    component: Layout,
    meta: {
      title: '系统管理',
      icon: 'fdddfont icon-setting',
      role: ['admin']
    },
    children: [{
        path: 'user-management',
        name: 'UserManagement',
        component: _import_('SystemManagement/User/index'),
        meta: {
          title: '用户管理',
          icon: 'fdddfont icon-personstalker'
        }
      },
      {
        path: 'role-management',
        name: 'RoleManagement',
        component: _import_('SystemManagement/Role/index'),
        meta: {
          title: '角色管理',
          icon: 'fdddfont icon-user-group'
        }
      },
      {
        path: 'menu-management',
        name: 'MenuManagement',
        component: _import_('SystemManagement/Menu/index'),
        meta: {
          title: '菜单管理',
          icon: 'fdddfont icon-menu3caidan3'
        }
      },
      {
        path: 'log-management',
        name: 'LogManagement',
        component: _import_('SystemManagement/Log/index'),
        meta: {
          title: '日志管理',
          icon: 'fdddfont icon-rizhi'
        }
      }
    ]
  }
]



