<template>
  <el-header v-show="!windowMaxState" class="header" :height="headerHeight">
    <div class="center">
      <span class="companyName">红星美凯龙</span>
      <header-shortcut :menu="shortcutMenu"/>
    </div>
    <div class="settings">
      <ul>
        <li>
          <div class="search">
            <input type="text" placeholder="搜索">
            <span class="searchIcon">
              <i class="el-icon-search"></i>
            </span>
          </div>
        </li>
        <li>
          <div class="addSome">
            <i class="el-icon-plus addIcon" ></i>
          </div>
        </li>
        <li>
          <div class="liBorder"></div>
        </li>
        <li>
         <span class="headerTitle">我的</span>
        </li>
        <li>
          <div class="liBorder"></div>
        </li>
        <li>
         <span class="headerTitle">日历</span>
        </li>
        <li>
          <div class="liBorder"></div>
        </li>
         <li>
          <div>
            <i class="el-icon-question titleIcon" ></i>
          </div>
        </li>
        <li>
          <div>
            <i class="el-icon-bell titleIcon" ></i>
          </div>
        </li>
        <li>
          <div>
            <i class="el-icon-message titleIcon" ></i>
          </div>
        </li>
        <li>
          <div>
            <i class="el-icon-setting titleIcon"></i>
          </div>
        </li>
      </ul>

    </div>
  </el-header>
</template>

<script>
import moment from "moment";
import { mapGetters } from "vuex";

import PersonalSetting from "./Settings";
import HeaderShortcut from "./HeaderShortcut";

// api
import { commonLogout } from "@/api/my";

export default {
  name: "Headers",
  data() {
    return {
      dayTime: "",
      commonDialog: null
    };
  },
  computed: {
    ...mapGetters([
      "userName",
      "avatar",
      "token",
      "shortcutMenu",
      "windowMaxState",
      "headerHeight"
    ])
  },
  mounted() {
    const now = parseInt(moment().format("HH"));
    if (now < 12) this.dayTime = "早上";
    else if (now >= 12 && now < 18) this.dayTime = "下午";
    else if (now >= 18 && now <= 23) this.dayTime = "晚上";
    else this.dayTime = "";
  },
  methods: {
    settingPersonal() {
      this.$router.push({ name: "PersonalSettings" });
    },
    modifyPassword() {
      this.$router.replace({ path: "/modify-password" });
    },
    logout() {
      this.$confirm("确认要登出系统吗？", "提醒", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(() => {
          commonLogout({ token: this.token }).then(res => {
            this.$store.dispatch("userLogout").then(() => {
              location.reload();
            });
          });
        })
        .catch(() => {});
    }
  },
  components: {
    PersonalSetting,
    HeaderShortcut
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.header {
  display: flex;
  padding: 0;
  // color: lighten($base-light-color, 100%);
  background-color: #ffffff;
  box-shadow:
  0 0 0 0 rgba(0, 0, 0, 0.14),
  0 0 0 0 rgba(0, 0, 0, 0.14),
  0 2px 4px 0 rgba(0, 0, 0, 0.14),
  0 0 0 0 rgba(0, 0, 0, 0.14);
  .companyName {
    font-size: 16px;
    color: #1f1f1f;
    letter-spacing: -0.41px;
    line-height: 48px;
    padding: 0 25px;
  }
  .center {
    flex: 1;
    position: relative;
  }
  .search {
    position: relative;
    input {
      width: 200px;
      height: 30px;
      border: none;
      background: #f5f5f5;
      border-radius: 14.5px;
      padding: 0 40px;
    }
    input:focus{
        outline:none;
        }
    .searchIcon {
      position: absolute;
      height: 100%;
      top: 0;
      left: 14px;
      font-size: 13px;
      i {
        height: 100%;
        text-align: center;
        transition: all 0.3s;
        line-height: 30px;
      }
    }
  }
  .addSome{
    width: 28px;
    height: 28px;
    border-radius: 14px;
    background-image: linear-gradient(-180deg, #2AA4FA 0%, #31BEFB 33%, #3EF0FF 98%);
    .addIcon{
      color: #FFFFFF;
      font-size: 18px;
      font-weight: 600;
      height: 100%;
      width: 100%;
      line-height: 28px;
      text-align: center;
    }
  }
  .liBorder{
    width: 1px;
    padding: 12px 0;
    background: #E7E9ED;
  }
  .headerTitle{
    font-size: 14px;
    color: #777777;
    letter-spacing: -0.41px;
    font-weight: 300;
  }
  .titleIcon{
    font-size: 18px;
    padding: 0 5px;
  }
  .user {
    display: inherit;
    @include flexCenter();

    span {
      display: inline-block;

      &.avatar {
        width: 20px;
        height: 20px;
        margin-right: 10px;
        border-radius: 20px;
        background-color: lighten($base-dark-color, 15%);
        overflow: hidden;

        > img {
          width: 20px;
          height: 20px;
        }
      }
      // &.name { }
    }
  }

  .settings {
    ul {
      display: flex;
      flex-direction: row;
      height: 100%;

      li {
        font-size: 1rem;
        display: inherit;
        @include flexCenter;
        flex: 1 auto;
        padding: 0 10px;
        cursor: pointer;

        span:before {
          margin-right: 5px;
        }

        > div {
          font-size: 1rem;
          font-weight: 300;
          color: $base-dark-color;
        }
      }

      .zvpfont:before {
        margin-right: 5px;
      }
    }
  }
}
</style>



